.Pokecard {
  border: 2px solid black;
  width: 250px;
  padding: 10px;
  margin: 1rem;
  box-shadow: 7px 10px 12px -5px rgb(0, 0, 0.5);
  background-color: hsl(350, 72%, 89%);
  border-radius: 3px;
}

.Pokecard-data {
  font-size: 80%;
}

.Pokecard-img {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 150px;
  background-color: antiquewhite;
  border-radius: 3px;
  margin-bottom: 1rem;
}

.Pokecard-title {
  margin-bottom: 1rem;
  font-weight: bold;
  font-size: 1.5rem;
}

.Pokecard img {
  transition: all ease 500ms;
}
.Pokecard img:hover {
  transform: scale(1.25);
}
